/**
* ===========================================================
* RECALL-FORM desktop
* ===========================================================
*/
.recall-form-wrapper {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0;
  background-color: #00b2ff;
}

.recall-form {
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
  text-align: left;
  color: #323232;
  padding: 25px 20px 112px 22px;
  background-color: #00b2ff;
  width: 1490px;
  margin: 0 auto;
  position: relative;
}

.recall-form__form {
  width: 572px;
  margin-left: 739px;
  padding-top: 37px;
    padding-left: 16px;
}

.recall-form__form::before {
  content: "";
  top: 94px;
    left: 2px;

    width: 665px;
    height: 487px;

  position: absolute;
  background-image: url("../img/airplane-dots.png");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  z-index: 100;
}

.recall-form__header {
  margin: 0;
    margin-top: 30px;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 50.63px;
}

.recall-form__tip {
  margin: 10px 0 47px 4px;
  color: #ffffff;
  font-family: 'PT Mono', monospace;
  font-size: 16px;
  font-weight: 400;
  line-height: 22.5px;
}


.recall-form__input-wrap {
  margin-bottom: 37px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.recall-form input[type="text"],
.recall-form input[type="tel"] {
  box-sizing: border-box;
  position: relative;

  padding: 16px 20px;

  width: 270px;
  min-width: 100px;

  color: #ffffff;
  font-family: 'PT Mono', monospace;
  font-size: 16px;
  font-weight: 400;
  line-height: 22.5px;

  border: none;
  background-color: rgba(255, 255, 255, 0.1);
  outline: none;

  &:hover {
    background-color: rgba(255, 255, 255, 0.3);
  }

  &:focus {
    background-color: rgba(255, 255, 255, 0.3);
  }
}

.recall-form input[type="text"]::placeholder,
.recall-form input[type="tel"]::placeholder {
  color: #ffffff;

  font-size: 16px;
  font-weight: 400;
  line-height: 22.5px;
  opacity: 1;
}

.recall-form input::-moz-placeholder {
  color: #000000;
  opacity: 1;
}

.recall-form textarea::-moz-placeholder {
  color: #000000;
  opacity: 1;
}

.recall-form__message-wrap {
  padding: 0;
  margin-bottom: 40px;
}

.recall-form__message-input {
  box-sizing: border-box;
  padding: 15px 20px;
  margin: 0;

  width: 100%;
  height: 154px;
  min-width: 150px;

  font-size: 18px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;

  color: #ffffff;
  font-family: 'PT Mono', monospace;
  font-size: 16px;
  font-weight: 400;
  line-height: 22.5px;

  border: none;
  background-color: rgba(255, 255, 255, 0.1);
  outline: none;
  overflow: auto;

  &:hover {
    background-color: rgba(255, 255, 255, 0.3);
  }

  &:focus {
    background-color: rgba(255, 255, 255, 0.3);
  }
}

.recall-form__message-input::placeholder {
  color: #ffffff;

  font-size: 16px;
  font-weight: 400;
  line-height: 22.5px;
  opacity: 1;
}

.checkbox-area {
  opacity: 0.8;
  color: #ffffff;
  font-family: 'PT Mono', monospace;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
position: relative;

}

.recall-form__checkbox-message {
  display: inline-block;

  margin: 0;
  position: relative;
  margin-top: 3px;
  margin-left: 344px;
}

.recall-form__checkbox-input+label::before {
  content: "";

  position: absolute;
  top: 6px;
    left: -33px;
    width: 14px;
    height: 15px;
  border: 1px solid #ffffff;
  background-color: #00b2ff;
}

.recall-form__checkbox-input:checked+label::after {
  content: '';

  transform: rotate(45deg);
  border-right: 3px solid rgba(248, 247, 244, 1);
  border-bottom: 3px solid rgba(248, 247, 244, 1);

  position: absolute;
  top: 8px;
  left: -26px;
  width: 3px;
  height: 6px;
}

.recall-form input[type="checkbox"] {
  position: absolute;
  top: 0;
  left: 0;

  width: 16px;
  height: 16px;
  border: 1px solid #ffffff;
  background-color: #00b2ff;
  cursor: pointer;

  line-height: 26px;

  &:hover {
    background-color: #e5e5e5;
  }

  &:focus {
    background-color: #e5e5e5;
    border: 0.5px dashed #81b3d2;
  }

  &:active {
    background-color: #d5d5d5;
  }
}

.recall-form__btn {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  margin-left: 4px;
  width: 270px;
height: 45px;

  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  background-color: #00b2ff;
  padding: 7px 19px 8px 18px;
  border-radius: 5px;
  border: 1px solid #ffffff;
}

